* {
  color: #67a45d;
}

.mp {
  padding: 10px;
  margin: 10px auto;
  width: 23rem;
}

.m-auto {
  margin: 0 auto;
  text-align: center;
}

.c-bw {
  background-color: white;
  color: #67a45d;
}

.c-bb {
  background-color: #090a0c;
  color: #fff;
}

.c-ui {
  background-image: linear-gradient(82deg, #6161fb, #6fed95);
  color: #fff;
}

.sh, .btn {
  box-shadow: 0 0 7px -2px #999696;
}

a, li {
  color: #67a45d;
  text-decoration: none;
}

.b-r, .btn {
  border-radius: 10px;
}

.b-r50 {
  border-radius: 50%;
}

/* ----------------------  cards ---------------------- */
/* ----------------------  cards ---------------------- */
/* ----------------------  cards ---------------------- */
/* ----------------------  cards ---------------------- */
/* ----------------------  cards ---------------------- */
.card {
  height: auto;
  width: 23rem;
  background-color: #fff;
  background-clip: border-box;
  border: 1px solid rgba(0, 0, 0, 0.125);
  display: inline-block;
  margin: 10px;
}

.b {
  width: 23rem;
}

.clip-1 {
  background-size: cover;
  background-position: -100px 0;
  background-repeat: no-repeat;
  -webkit-clip-path: ellipse(90% 70% at 0% 50%);
  clip-path: ellipse(90% 70% at 50% 20%);
  -webkit-shape-outside: ellipse(90% 70% at 50% 20%);
  shape-outside: ellipse(90% 70% at 50% 70%);
  -webkit-shape-margin: 2em;
  shape-margin: 2em;
}

.an2 {
  margin: 15px auto;
  height: 36px;
  width: 240px;
  padding: 0px;
  font-size: 14px !important;
  line-height: 1 !important;
}

.an2 .appName {
  width: 235px;
}

.appName {
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 7px;
}

.btn {
  padding: 10px;
  margin: 5px;
  color: white;
  background-color: #67a45d;
}

/* ----------------------  nav ---------------------- */
/* ----------------------  nav ---------------------- */
/* ----------------------  nav ---------------------- */
/* ----------------------  nav ---------------------- */
.nav {
  box-sizing: border-box;
  height: 60px;
  margin: 0 auto;
  max-width: 400px;
}

.nav li {
  list-style-type: none;
  padding: 19px 0 20px 0;
  border-radius: 10px;
  font-size: 18px;
  width: 20%;
  float: right;
}

.logo {
  margin: -10px 20px  0px 0px;
  width: 40px;
  height: 40px;
  float: right;
}

